<template>
  <div>
    <div class="form-group">
      <label for="qnaTitle">질문제목</label>
      <input
        type="text"
        class="form-control"
        id="qnaTitle"
        ref="qnaTitle"
        placeholder="질문 제목"
        v-model="qnaTitle"
      />
    </div>

    <div class="form-group">
      <label for="qnaContent">질문내용</label>
      <textarea
        type="text"
        class="form-control"
        id="qnaContent"
        ref="qnaContent"
        placeholder="내용을 입력하세요"
        v-model="qnaContent"
      ></textarea>
    </div>

    <div class="text-right">
      <button class="btn btn-primary" @click="checkHandler">수정</button>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data: function() {
    return {
      qnaNo: "",
      qnaTitle: "",
      qnaContent: "",
      qnaUserid: "",
      qnaDatetime: "",
      replyContent: "",
      replyDatetime: "",
      replyUserid: "",
    };
  },
  methods: {
    checkHandler() {
      let err = true;
      let msg = "";
      !this.qnaTitle &&
        ((msg = "질문제목을 입력해주세요"),
        (err = false),
        this.$refs.qnaTitle.focus());
      err &&
        !this.qnaContent &&
        ((msg = "질문내용을 입력해주세요"),
        (err = false),
        this.$refs.qnaContent.focus());

      if (!err) alert(msg);
      else this.updateHandler();
    },
    updateHandler() {
      axios
        .put(`http://localhost:8080/api/qna/${this.qnaNo}`, {
          qnaNo: this.qnaNo,
          qnaTitle: this.qnaTitle,
          qnaContent: this.qnaContent,
          qnaUserid: this.qnaUserid,
          qnaDatetime: new Date(),
          replyContent: this.replyContent,
          replyUserid: this.replyUserid,
          replyDatetime: this.replyDatetime,
        })
        .then(({ data }) => {
          let msg = "수정 처리시 문제가 발생했습니다.";
          if (data === "success") {
            msg = "수정이 완료되었습니다.";
          }
          alert(msg);
          this.$router.push("/list");
        });
    },
  },
  created() {
    const params = new URL(document.location).searchParams;
    axios
      .get(`http://localhost:8080/api/qna/${params.get("qnaNo")}`)
      .then(({ data }) => {
        this.qnaNo = data.qnaNo;
        this.qnaTitle = data.qnaTitle;
        this.qnaContent = data.qnaContent;
        this.qnaUserid = data.qnaUserid;
        this.qnaDatetime = data.qnaDatetime;
        this.replyContent = data.replyContent;
        this.replyDatetime = data.replyDatetime;
        this.replyUserid = data.replyUserid;
      });
  },
};
</script>

