import Vue from "vue";
import App from "./components/App.vue";
import router from "./router";
import VueSession from "vue-session";

var sessionOption = {
  persist: true,
};

Vue.config.productionTip = false;
Vue.use(VueSession, sessionOption);
new Vue({
  router,
  render: (h) => h(App),
}).$mount("#app");
