<template>
  <div class="container">
    <table class="table table-bordered ">
      <tr>
        <th>질문번호</th>
        <td>{{ item.qnaNo }}</td>
      </tr>
      <tr>
        <th>질문제목</th>
        <td>{{ item.qnaTitle }}</td>
      </tr>
      <tr>
        <td colspan="2">{{ item.qnaContent }}</td>
      </tr>
      <tr>
        <th>질문자아이디</th>
        <td>{{ item.qnaUserid }}</td>
      </tr>
      <tr>
        <th>질문일시</th>
        <td>{{ getFormatDate(item.qnaDatetime) }}</td>
      </tr>
    </table>

    <br />
    <div class="text-center">
      <button class="btn btn-secondary" @click="replyR">답변보기</button>
      <router-link :to="'/list'"
        ><button class="btn btn-primary">목록</button></router-link
      >
      <router-link :to="'/update?qnaNo=' + item.qnaNo"
        ><button class="btn btn-primary">수정</button></router-link
      >
      <router-link :to="'/delete?qnaNo=' + item.qnaNo"
        ><button class="btn btn-primary">삭제</button></router-link
      >
    </div>
    <div v-if="replyFlag == true">
      <div v-if="item.replyContent">
        <table class="table table-bordered ">
          <tr>
            <th>답변자아이디</th>
            <td>{{ item.replyUserid }}</td>
          </tr>
          <tr>
            <th>답변일시</th>
            <td>{{ getFormatDate(item.replyDatetime) }}</td>
          </tr>
        </table>
      </div>
      <div v-else>
        <h3>답변이 없습니다.</h3>
      </div>
      <div class="form-group">
        <label for="replyContent">답변내용</label>
        <textarea
          type="text"
          class="form-control"
          id="replyContent"
          ref="replyContent"
          placeholder="내용을 입력하세요"
          v-model="replyContent"
        ></textarea>
      </div>
      <div class="text-right">
        <button class="btn btn-primary" @click="replyPut">답변달기</button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import moment from "moment";

export default {
  name: "read",
  data: function() {
    return {
      item: {},
      replyFlag: false,
    };
  },
  created() {
    this.replyFlag = false;
    const params = new URL(document.location).searchParams;
    axios
      .get(`http://localhost:8080/api/qna/${params.get("qnaNo")}`)
      .then(({ data }) => {
        this.item = data;
      });
  },
  methods: {
    getFormatDate(regtime) {
      return moment(new Date(regtime)).format("YYYY.MM.DD HH:mm:ss");
    },
    replyR() {
      this.replyFlag = true;
    },
    replyPut() {
      axios.get("http://localhost:8080/api/qna/session", {}).then((data) => {
        this.replyUserid = data.data;
        this.putrp();
      });
    },
    putrp() {
      axios
        .put(`http://localhost:8080/api/qna/${this.qnaNo}`, {
          qnaNo: this.qnaNo,
          qnaTitle: this.qnaTitle,
          qnaContent: this.qnaContent,
          qnaUserid: this.qnaUserid,
          qnaDatetime: this.qnaDatetime,
          replyContent: this.replyContent,
          replyUserid: this.replyUserid,
          replyDatetime: new Date(),
        })
        .then(({ data }) => {
          let msg = "답변 수정 처리시 문제가 발생했습니다.";
          if (data === "success") {
            msg = "답변 수정이 완료되었습니다.";
          }
          alert(msg);
          this.$router.push("/read");
        });
    },
  },
};
</script>

<style></style>
